<div class="the-header"><div class="logo-or-name" @click="back"><img class="logo" src="static/logo/logoNav.png"><div class="name">{{ softConfig.softName }}</div></div><div class="navigation-bar" v-if="!user.student"><div class="items" v-for="(item, index) in navList" :class="{ active: index === active }" @click="changePath(item.path)">{{ item.name }}</div></div><div class="navigation-bar" v-else></div><div class="user-profile" ref="userProfile"><a-badge><b-icon type="icontongzhi4" :style="{ fontSize: '24px', cursor: 'pointer' }"></b-icon></a-badge><a-popover placement="bottom" :getPopupContainer="() =&gt; $refs.userProfile"><template #content><div class="menu"><div class="menu-item align-items-center" @click="changePath('/Teacher/userInfo')"><b-icon type="iconzhanghao" :style="{ fontSize: '16px' }"></b-icon><span>个人中心</span></div><div class="menu-item align-items-center" @click="changePath('/Teacher/exhibition')"><b-icon type="iconchakan" :style="{ fontSize: '16px' }"></b-icon><span>组件展示</span></div><div class="menu-item align-items-center" @click="outLogin"><b-icon type="icontuichu11" :style="{ fontSize: '16px' }"></b-icon><span>退出账号</span></div></div></template><div class="user"><a-avatar shape="square" :size="32" :src="user.headImagePath"></a-avatar><div class="name">{{ user.name }}</div></div></a-popover></div></div>